  animation: rotating 1s linear infinite;
  position: relative;
}

.is-displayed:before {
  content: "";
  width: 60px;
  height: 60px;
  background: #f0f0f0;
  border-radius: 50px;
  position: absolute;
  top: 10px;
  left: 10px;
