@keyframes Move {
  0% {
    transform-origin: center;
    transform: scale(0);
  }

  100% {
    transform-origin: center;
    transform: scale(1);
  }
}

@keyframes Bounce {
  0% {
    transform: scale(1)translateY(0);
  }

  10% {
    transform: scale(1.1, .9)translateY(0);
  }

  30% {
    transform: scale(.9, 1.1)translateY(-20px);
  }

  50% {
    transform: scale(1.05, .95)translateY(0);
  }

  57% {
    transform: scale(1)translateY(-7px);
  }

  64% {
    transform: scale(1)translateY(0);
  }

  100% {
    transform: scale(1)translateY(0);
  }
}

@keyframes Heart {
  0% {
    transform: scale(1);
  }

  5% {
    transform: scale(1.2);
  }

  10% {
    transform: scale(1);
  }

  15% {
    transform: scale(1.5);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

